@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*[class*=title],
*[class*=heading],
*[class*=text] {
  margin: 0;
}

*[class*=link] {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.block-center {
  width: calc(100% - 40px);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .block-center {
    width: calc(100% - 60px);
    max-width: 708px;
  }
}
@media (min-width: 1190px) {
  .block-center {
    width: calc(100% - 200px);
    max-width: 1220px;
  }
}

.button {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
}

.page--column {
  height: 100%;
}

.page__body {
  margin: 0;
  min-width: 280px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
  font-size: 14px;
  line-height: 18px;
}
.page__body--column {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.page__header {
  padding: 10px 0 17px;
}

.page__content--column {
  flex-grow: 1;
}

.page__footer {
  padding: 39px 0 22px;
  color: #444444;
  background-color: #f2f2f2;
}

.page-header {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}

.page-header__logo {
  margin: 0;
}

.page-header__nav {
  margin: 0;
}

.title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.sub-title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}

.navigation {
  width: fit-content;
}

.navigation__toggler {
  width: 24px;
  height: 16px;
}

.navigation__menu {
  display: flex;
  flex-direction: column;
}

.menu {
  display: flex;
  flex-direction: column;
  list-style-type: none;
}

.menu__item {
  padding: 22px 0 24px;
}

.menu__link {
  color: inherit;
  font: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.contacts {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
}

.contacts__info {
  display: flex;
  padding: 26px 0;
  color: #000000;
  background-color: #ffffff;
}

.contacts__heading {
  max-width: 139px;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}

.contacts__address {
  max-width: 139px;
  margin: 0;
  color: #444444;
}

.contacts__map {
  width: 100%;
  max-height: 362px;
  object-fit: cover;
}

.contacts__location {
  position: absolute;
  left: 50%;
  bottom: 50%;
  transform: translate(-50%, -50%);
}

.page-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 39px 0 22px 0;
}

.page-footer__logo {
  margin-bottom: 32px;
}

.page-footer__menu {
  margin-bottom: 22px;
}

.page-footer__link {
  display: flex;
  text-decoration: none;
}

.socials {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 281px;
  min-height: 63px;
  margin: 0 0 22px 0;
}

.socials__link {
  display: block;
  outline: 1px solid #a21c32;
  outline-offset: -1px;
}
.socials__link--vk {
  width: 23px;
  height: 16px;
}
.socials__link--instagramm {
  width: 23px;
  height: 16px;
}
.socials__link--facebook {
  width: 23px;
  height: 16px;
}

.designers {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 281px;
}

.designers__name {
  font-size: 16px;
  line-height: 18px;
}

.designers__image {
  width: 27px;
  height: 34px;
}

.logo img {
  display: block;
}

/* Стили для Главная страница */
.main-page {
  background-color: #ffffff;
}

.main-page__section {
  margin-bottom: 20px;
}

.hero {
  padding: 27px 35px 0;
  color: #ffffff;
  background-color: #68b738;
  background-image: url("../img/backgrounds/hero-mobile.webp");
}

.hero__title {
  max-width: 250px;
  margin: 0 auto;
}

.hero__slogan {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
}

.example {
  padding: 25px 0 40px 0;
  color: #444444;
  background-color: #f2f2f2;
}

.example__title {
  margin-bottom: 40px;
}

.example__text {
  margin-bottom: 20px;
}

.example__progress {
  margin: 0 0 20px 0;
}

.example__parameter {
  font-size: 12px;
  line-height: 12px;
}

.example__value {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
}

.example__costs {
  margin: 0 0 40px 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.categories {
  display: flex;
  flex-direction: column;
}

.categories__item {
  margin-top: 20px;
}

.category {
  padding: 21px 18px 19px;
  color: #444444;
  background-color: #f2f2f2;
}

.category__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
}

.category__text {
  margin: 0;
}

.category__link {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}

.process {
  color: #444444;
  margin-bottom: 49px;
}

.process__title {
  margin-bottom: 40px;
  color: #000000;
}

.process__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  list-style-type: none;
}

.process__item {
  display: flex;
}
.process__item:not(:last-of-type) {
  margin-bottom: 20px;
}
.process__item::before {
  flex-shrink: 0;
  display: block;
  width: 60px;
  height: 60px;
  margin-right: 20px;
  color: #ffffff;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-size: 30px 32px;
  background-position: center;
  content: "";
}
.process__item--useful::before {
  background-image: url("../img/index/mobile/icon-useful.svg");
}
.process__item--powder::before {
  background-image: url("../img/index/mobile/icon-powder.svg");
}
.process__item--meal::before {
  background-image: url("../img/index/mobile/icon-meal.svg");
}
.process__item--results::before {
  background-image: url("../img/index/mobile/icon-results.svg");
}

.process__text {
  align-self: center;
}

/* Стили для Страница каталога */
.catalog-page {
  background-color: #ffffff;
}

/* Стили для Страница формы заказов */
.order-page {
  background-color: #ffffff;
}